export default {
	data() {
		return {
			money: uni.getStorageSync("userInfo").balance,
			list: []
		}
	},
	onLoad() {
		let _this = this;
		_this.request({
			url: _this.http.getDriverDealList,
			data: {
				pageNum: '1',
				pageSize: '10000',
				driverId: uni.getStorageSync("userInfo").driverId
			},
			success: (res)=>{
				if(res.code === "0"){
					_this.list = res.result;
				}else{
					uni.showToast({
						icon: 'none',
						title: res.message
					})
				}
			}
		})
	},
	methods: {
		goGrtMoney(){
			if(!this.money) return uni.showToast({
						icon: 'none',
						title: "无余额可提现!"
					})
			uni.navigateTo({
				url: "/pages/withdrawal/withdrawal"
			});
		}
	}
}
